import { Component, OnInit, Input, Output, EventEmitter, ChangeDetectorRef } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import { ControlContainer, NgForm, FormsModule } from '@angular/forms';

import { VendorService } from 'src/app/api/controllers/Vendor';
import { AccountsService } from 'src/app/api/controllers/Accounts';
import { SharedService } from '../../../../shared/services/shared.service';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';
import * as moment from 'moment';


@Component({
  selector: 'app-expense-post-multi-invoice',
  templateUrl: './expense-post-multi-invoice.component.html',
  styleUrls: ['./expense-post-multi-invoice.component.scss']
})
export class ExpensePostMultiInvoiceComponent implements OnInit {

  invoice:any;
  invoiceId:string = "";
  invoiceGLAccountsData:any;
  invoiceGLAccountsArray:any;

  isEditInvoice:boolean = false;
  isSingleInvoice:boolean = false;
  isNewInvoice:boolean = false;

  isInvoiceSubmitted:boolean = true;

  vendorDataList:any;
  isVendorDataLoaded:boolean = false;
  vendorCategoryData:any;

  isFormInValid:boolean = true;

  vatTypeDataList:any;
  discountFinalTypeDataList:any;

  constructor(
  	private router: Router,
    private route: ActivatedRoute,
    private vendorService:VendorService,
    private lookupService: LookupService,
    private accountsService:AccountsService,
  	private sharedService:SharedService,
  	private cookieService:CookieService
  ) { }

  getCategoryName(id){
    
    var data = _.filter( this.vendorCategoryData, function( item ) {
      if(item.lookupValueId === parseInt(id))
        return item;
    });

    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      return data[0].lookupValueName;
    }

  }

  addInvoice() {

  	let details =  {
        "apartmentId": parseInt(this.cookieService.get('apartmentId')),
        "invoiceId": 0,
        "glaccountId": this.invoiceGLAccountsData.glaccountId,
        "comments": "",
        "form": false,
        "isActive": true,
        "insertedBy": parseInt(this.cookieService.get('userId')),
        "insertedOn": new Date().toISOString(),
        "updatedBy": null,
        "updatedOn": new Date().toISOString(),
        "tax1": 0,
        "tax2": 0,
        "tax3": 0,
        "deductTax": 0,
        "amount": this.invoiceGLAccountsData.amount
    }

    this.invoiceGLAccountsArray.push(details);

    _.each(this.invoiceGLAccountsArray, item => {

      if(item.form){
        this.isFormInValid = false;
      }
      else {
        this.isFormInValid = true;
      }

    })

  }

  getFieldParams(event){
    this.invoiceGLAccountsArray = event;

    _.each(this.invoiceGLAccountsArray, item => {

      if(item.form){
        this.isFormInValid = false;
      }
      else {
        this.isFormInValid = true;
      }

    })

  }


  submitExpenseMultiInvoiceForm(form){
    

    this.isInvoiceSubmitted = false;

  	var self = this;

    this.invoiceGLAccountsArray.map(function(item) {
      self.invoice.vendorInvoiceAmount = self.invoice.vendorInvoiceAmount + item.amount; 
      delete item.form; 
      return item; 
    });

    let details = {

      "apartmentId": parseInt(this.cookieService.get('apartmentId')),
      "vendorId": parseInt(this.invoice.vendorId),
      "vendorInvoiceNumber": this.invoice.vendorInvoiceNumber,
      "vendorInvoiceAmount": this.invoice.vendorInvoiceAmount,
      "vendorInvoiceDate": new Date().toISOString(),
      "dueDate": this.invoice.vendorInvoiceDate,
      "stateGst": 0,
      "centralGst": 0,
      "integratedGst": 0,
      "isDeductTds": true,
      "deductTds": 0,
      "deductTdstypeId": 1,
      "expenseHeadId": 1,
      "payeeId": 1,
      "isEmailSent": true,
      "isSmssent": true,
      "voucherNumber": null,
      "vendorInvoiceStatusId": null,
      "postedBy": parseInt(this.cookieService.get('userId')),
      "postedOn": new Date().toISOString(),
      "comments": this.invoice.comments || "",
      "isActive": true,
      "insertedBy": parseInt(this.cookieService.get('userId')),
      "insertedOn": new Date().toISOString(),
      "updatedBy": 0,
      "updatedOn": new Date().toISOString(),
      "transReference1": "string",
      "transReference2": "string",
  	  "vendorInvoiceGlaccount": this.invoiceGLAccountsArray

    }
    
    let params = {
      invoice: details
    }

  	this.accountsService.addVendorInvoice(params).subscribe((res:any) => {

  		 	if(res.message){

    			this.isInvoiceSubmitted = true;
    			this.sharedService.setAlertMessage("Expense added successfully");
    			this.router.navigate(['ams/expense/subledger']);

  	    }
  	    else {

    			this.isInvoiceSubmitted = true;
  	     
  	    }

  		  },
  	error => {

  		this.isInvoiceSubmitted = true;

  	});

    }

  ngOnInit() {

  	this.invoice = {};
  	this.invoice.vendorId = this.route.params['value'].id;

  	this.invoice.vendorInvoiceAmount = 0;
    this.invoiceGLAccountsData = {};

    this.invoiceGLAccountsData.glaccountId = "";
    this.invoiceGLAccountsData.amount = "";
    this.invoiceGLAccountsData.comments = "";

    this.invoiceGLAccountsArray = [
      {
        "apartmentId": parseInt(this.cookieService.get('apartmentId')),
        "invoiceId": 0,
        "glaccountId": this.invoiceGLAccountsData.glaccountId,
        "comments": this.invoiceGLAccountsData.comments,
        "isActive": true,
        "insertedBy": parseInt(this.cookieService.get('userId')),
        "insertedOn": new Date().toISOString(),
        "updatedBy": null,
        "updatedOn": new Date().toISOString(),
        "tax1": 0,
        "tax2": 0,
        "tax3": 0,
        "deductTax": 0,
        "amount": this.invoiceGLAccountsData.amount
      }
   ];

   let dateParams = {
    LookupTypeId: 74
   }

   this.lookupService.getLookupValueByLookupTypeId(dateParams).subscribe((res:any) => {
    this.invoice.dueDate = moment().add(parseInt(res[0].lookupValueName), 'days');
   })

   let vatListParams = {
    LookupTypeId: 77
   }

   //VAT types
   this.lookupService.getLookupValueByLookupTypeId(vatListParams).subscribe((res:any) => {
    this.vatTypeDataList = res;
     _.each(this.vatTypeDataList, item => {
       item.custVatTypeAmount = 0;
     });

  });

  let disListParams = {
    LookupTypeId: 88
  }

  //discount types
  this.lookupService.getLookupValueByLookupTypeId(disListParams).subscribe((res:any) => {
    this.discountFinalTypeDataList = res;
  });

  //for post single invoice
  if(this.route.params['value'].type == 'single' && this.route.params['value'].invoiceid == undefined){
    this.invoiceGLAccountsArray.splice(0,1);
    this.isSingleInvoice = true;
    this.isEditInvoice = false;
  }

  //for post multi invoice
  if(this.route.params['value'].type != 'single' && this.route.params['value'].invoiceid == undefined){
   this.isSingleInvoice = false;
   this.isEditInvoice = false;
  }

  if(this.route.params['value'].invoiceid != undefined){
    
    this.isInvoiceSubmitted = false;
    this.isFormInValid = false;
    this.isEditInvoice = true;

  }

  let vendorParams = {
    apartmentId: parseInt(this.cookieService.get('apartmentId'))
  }

  this.vendorService.getVendorByApartmentId(vendorParams).subscribe((res:any) => {

    this.vendorDataList = res.filter(item => {
      return item.vendorId == this.route.params['value'].id;
    });

    this.isVendorDataLoaded = true;

  });


  }

}
